* {
  margin: 0;
  padding: 0;
}

#root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  outline: none;
}

.custom-sb::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.custom-sb::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px grey;
  border-radius: 4px;
}
.custom-sb::-webkit-scrollbar-thumb {
  background: rgb(165, 165, 165);
  border-radius: 4px;
}
.custom-sb::-webkit-scrollbar-thumb:hover {
  background: rgb(185, 185, 185);
}

.hide-sb {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.hide-sb::-webkit-scrollbar {
  display: none; /* Chrome, Safari and Opera */
}

.glowing {
  -webkit-animation: glow 0.5s ease-in-out infinite alternate;
  -moz-animation: glow 0.5s ease-in-out infinite alternate;
  animation: glow 0.5s ease-in-out infinite alternate;
}
@keyframes glow {
  from {
    box-shadow: 0 0 2px 1px #fff, 0 0 3px 1px #fff, 0 0 4px 1px #fff,
      0 0 5px 1px #fff, 0 0 6px 1px #fff;
  }
  to {
    box-shadow: 0 0 4px 1px #fff, 0 0 6px 1px #fff, 0 0 8px 1px #fff,
      0 0 10px 1px #fff, 0 0 12px 1px #fff;
  }
}

@media (min-width: 1025px) {
  button:not(.unavailable) {
    cursor: pointer;
  }
  .pointer {
    cursor: pointer;
  }
  .glow-on-hover {
    opacity: 0.9;
  }
  .glow-on-hover:hover {
    opacity: 1;
  }
  .zoomin-on-hover {
    overflow: hidden;
  }
  .zoomin-on-hover:hover {
    transform: scale(1.05);
  }
  .zoomin-on-hover:hover > img {
    transform: scale(1.2);
  }
}

.full-h {
  height: 100%;
}
.full-w {
  width: 100%;
}
.overflow-hidden {
  overflow: hidden;
}
.overflow-auto {
  overflow: auto;
}

.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.capitalize {
  text-transform: capitalize;
}
.unavailable {
  opacity: 0.6;
}
.opacity-9 {
  opacity: 0.9;
}
.cmn-shadow {
  box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
    rgb(0 0 0 / 14%) 0px 3px 4px 0px, rgb(0 0 0 / 12%) 0px 1px 8px 0px;
}

.no-apr {
  appearance: none;
}
.bg-icon {
  background-size: contain;
  background-repeat: no-repeat;
}

.arrow-black {
  background-image: url(https://cdn4.iconfinder.com/data/icons/ionicons/512/icon-arrow-down-b-512.png);
}
.arrow-white {
  background-image: url(https://www.freeiconspng.com/uploads/white-down-arrow-png-2.png);
}

.main-select {
  background-origin: content-box;
  background-position: 100%;
}
.ms-pad {
  padding: 0.25rem calc(200% / 15);
}
.poss-pad {
  padding: 0.25rem 0.75rem 0.25rem 1.25rem;
}
.ss-select {
  background-position: 0.375rem;
}

.radius-50 {
  border-radius: 50%;
}

.relative {
  position: relative;
}

.flex {
  display: flex;
}
.flex-wrap {
  display: flex;
  flex-wrap: wrap;
}
.flex-col {
  display: flex;
  flex-direction: column;
}
.grow-1 {
  flex-grow: 1;
}

.justify-start {
  justify-content: flex-start;
}
.justify-center {
  justify-content: center;
}
.justify-end {
  justify-content: flex-end;
}
.justify-between {
  justify-content: space-between;
}
.justify-around {
  justify-content: space-around;
}

.align-start {
  align-items: flex-start;
}
.align-center {
  align-items: center;
}
.align-end {
  align-items: flex-end;
}
.align-between {
  align-items: space-between;
}

